export default {
  fonts: {
    main:
      "Montserrat, Segoe UI, Apple SD Gothic Neo, Lucida Grande, Lucida Sans Unicode, sans‑serif",
    code: "Roboto Mono, monospace",
  },
  colors: {
    mainWhite: "#F3FCF0",
    mainPurple: "#540D6E",
    secondaryPurple: "#72288d",
    mainMagenta: "#EE4266",
    mainYellow: "#FFD23F",
    mainBlack: "#1F271B",
    secondaryPink: "#F3EEEE",
    backgroundPurple: "#ae76c4",
    backgroundBlue: "#7677c4",
  },
  breakpoints: {
    mobileAndTablet: "only screen and (max-width: 600px)",
    largeDevices: "only screen and (min-width: 600px)",
    tablet: "only screen and (max-width: 1000px)",
  },
  spacings: {
    xxSmall: ".25rem",
    xSmall: ".5rem",
    small: "1rem",
    medium: "2rem",
    large: "3rem",
    xLarge: "4rem",
    xxLarge: "6rem",
  },
  animations: {
    button: "box-shadow 0.3 ease",
    link: "color 0.2 ease",
    transitionSpeed: "600ms",
  },
  shadows: {
    shadow1: "0px 5px 20px rgba(30, 30, 31, 0.05)",
  },
  width: {
    mainContentWidth: "2 / span 12",
  },
}
